___CSS_LOADER_EXPORT___.push([module.id, `.linked-node-map-RootContainer, .linked-node-map-canvas-div-container {
	position: relative;
	width: 100%;
	height: 100%;
	box-sizing: border-box;
}


.linked-node-map-canvas-div-container {
	overflow: hidden;
}

.linked-node-map-canvas {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}

.linked-node-map-svg-container {
	height: 35px;
}

.linked-node-map-SearchContainer {
	position: relative;
	font-size: 12px;
	position: relative;
	width: 100%;
	height: 100%;
}

.linked-node-map-SearchBox {
	position: absolute;
	top: 0;
	display: none;
}

.linked-node-map-SearchBarDD {
	width: 100%;
	max-height: 200px;
	overflow: auto;
	scrollbar-width: thin;
}

.linked-node-map-SearchContainer:hover div, .linked-node-map-SearchBox:hover div
	{
	display: block;
}

.linked-node-map-ToolBarPosition {
	min-height: 100px;
	position: absolute;
	top: 30px;
	right: 5px;
}

.linked-node-map-dark {
	border-color: white;
	background-color: black;
	color: white;
}

.linked-node-map-light {
	border-color: black;
	background-color: white;
	color: black;
}

.linked-node-map-relativeTag {
	position: relative;
}

.linked-node-map-ddRowStyle {
	padding-left: 5px;
	cursor: pointer;
}

.linked-node-map-ddRowStyle:hover {
	background-color: lightgrey;
}

.linked-node-map-ZoomButton, .linked-node-map-ResetButton {
	cursor: pointer;
	text-align: center;
	border: solid 1.5px;
	border-radius: 7px;
	display: inline-block;
	width: 50%;
}

.linked-node-map-ResetButton {
	display: block;
	width: 35px;
	font-size: 9px;
}

.linked-node-map-Tooltip {
	overflow: auto;
	scrollbar-width: thin;
	max-height: 250px;
	border-radius: 6px;
	padding: 3px 3px;
	font-size: 10px;
	background-color: rgba(255, 255, 255, 0.95);
	color: black;
	box-shadow: 1px 1px 5px #63738A;
	border: 1px solid #D8DDE6;
	white-space: pre-wrap;
	overflow: auto;
	font-weight: normal !important;
	position: absolutelinked-node-map-theme-;
}

.linked-node-map-TooltipHeading {
	border-bottom: 1px solid #63738A;
	font-size: inherit;
	text-transform: capitalize;
	white-space: nowrap;
	padding-right: 4px;
}

.linked-node-map-TooltipRow {
	color: black;
	white-space: nowrap;
}
